import { Injectable } from '@angular/core';
import { FiltersApi } from '@core/data-access/apis/filters.api';
import { PropertyWorthsApi } from '@core/data-access/apis/property-worth.api';
import { SectionsState } from '@core/data-access/states/sections.state';
import { CommonResponse } from '@core/models';
import { Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class PropertyWorthFacade {
	constructor(
		private readonly filterApi: FiltersApi,
		private propertyWorth: PropertyWorthsApi,
		private sectionState: SectionsState
	) {}

	getSectionList() {
		return this.sectionState.getSortedAisleList$('properties-for-sale');
	}

	getFilterUnits() {
		return this.filterApi.getFilterValue('property-units', { aisle: 'rent-commercial' });
	}

	postPropertyWorth(data) {
		return this.propertyWorth.postPropertyWorth(data);
	}

	postCommercialEstimation(data) {
		return this.propertyWorth.postCommercialEstimation(data);
	}

	getPurposeOptions(): Observable<CommonResponse<[]>> {
		return this.propertyWorth.getPurposeOptions();
	}
	postInPersonValuation(id: string) {
		return this.propertyWorth.postInPersonValuation(id);
	}
}
